<template>
  <el-row class="topDelete" >
    <el-col>
      <el-card shadow="always">
        <span class="messBox">{{name}}</span>
        <span class="messBox">{{id}}</span>
        <span class="messBox">{{major}}</span>
        <el-popover class="dele"
                    placement="top"
                    width="160"
                    v-model="visible2">
          <p></p>
          <div style="text-align: right; margin: 0">
            <el-button size="mini" type="text" @click="visible2 = false"></el-button>
            <el-button type="primary" size="mini" @click="deleteStudent"></el-button>
          </div>
          <el-button type="primary" icon="el-icon-delete" size="mini" slot="reference"></el-button>
        </el-popover>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
  import apartmentManage from '@/api/apartmentManage'
    export default {
      name: "Delete",
      data(){
        return{
          name:'',
          id:'',
          major:'',
          bunkId:'',
          visible2: false,
        }
      },
      mounted(){
        let that=this;
        that.getStudentInfo();
        this.loginControl();  //
      },
      methods:{
        getStudentInfo(){
          let that=this;
          that.sendbunkId.$on('toChangeStudentInfo',function (bunkId) {
            if(bunkId){
              that.bunkId=bunkId;
              console.log("recived bunkId is:"+bunkId);
              apartmentManage.getStudentInfobybunkId(bunkId).then(function (resp) {
                if(resp.data.code==200){
                  that.name=resp.data.data.name;
                  that.id=resp.data.data.studentNumber;
                  that.major=resp.data.data.major;
                }
              })
                .catch(function (err) {
                  console.log(err);
                })
            }else{
              that.bunkId='';
              that.name='';
              that.id='';
              that.major='';
            }
          })
        },
        deleteStudent(){
          let that=this;
          that.visible2=false;
          console.log("ready to delete bunkId is"+that.bunkId);
          if(that.bunkId){
            apartmentManage.deleteStudentByBunkId(that.bunkId).then(function (resp) {
              if(resp.data.code==200){
                that.bunkId='';
                that.name='';
                that.id='';
                that.major='';
                that.$message.success("");
              }else{
                that.$message.warning("");
              }
            })
              .catch(function (err) {
                console.log(err);
              })
          }else{
            that.$message.warning("");
          }

        }
      }
    }
</script>

<style scoped>
 .topDelete{
  width: 88%;
  margin: 15px auto;
}
  .messBox{
    margin-right: 60px;
  }
  .dele{
    float: right;
    margin-top: -5px;
  }

</style>



