import Vue from "vue";
import Router from "vue-router";

import Index from "@/views/Index";

import Introduce from "@/views/Introduce";

import Login from "@/views/Login";

import Import from "@/views/InfoManage/Import"; //
import Output from "@/views/InfoManage/Output"; //

import Building from "@/views/apartmentManage/Building"; //
import apartmentDelete from "@/views/apartmentManage/apartmentDelete"; //
import apartmentDistribute from "@/views/apartmentManage/apartmentDistribute"; //

import Student from "@/views/query/Student"; //
import Worker from "@/views/query/Worker"; //

import buildingPower from "@/views/userManage/buildingPower"; //
import userImport from "@/views/userManage/userImport"; //

import dormitoryAdjust from "@/views/dormitoryAdjust/dormitoryAdjust"; //

import apartment from '@/views/apartment'      //Alink
import test from '@/views/test'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path:"/index",
      component:Index,
    },
    {
      path:"/",
      component:Introduce
    },
    {
      path:"/login",
      component:Login
    },
    {
      path: "/manage/building",
      component: Building
    },
    {
      path: "/manage/distribute",
      component: apartmentDistribute
    },
    {
      path: "/manage/delete",
      component: apartmentDelete
    },
    {
      path: "/info/mangae/input",
      component: Import
    },
    {
      path: "/info/mangae/output",
      component: Output
    },
    {
      path: "/dormitory/adjust",
      component: dormitoryAdjust
    },
    {
      path: "/query/student",
      component: Student
    },
    {
      path: "/query/worker",
      component: Worker
    },
    {
      path: "/user/manage/building/power",
      component: buildingPower
    },
    {
      path: "/user/manage/import",
      component: userImport
    },
    {
      path:"/index/apartment",
      component:apartment
    },
    {
      path:"/mytest/test",
      component:test
    },
  ]
});



