<template>
  <el-card class="tpl" :body-style="{padding: '10px 10px 10px 20px'}">
    <el-row>
      <el-col :span="24">
        <div class="tpl-tit">
          <span class="act"></span>
        </div>
        <h4></h4>
        <div class="desc"></div>
        <div class="btns">
          <div>
            <i class="el-icon-download"></i> <a :href="InfoDownloadLink" target="new_window"></a>
          </div>
          <div @click="showDemo = true">
            <i class="el-icon-document"></i> <span></span>
          </div>
          <div @click="dialogVisible=true">
            <i class="el-icon-info"></i> <span></span>
          </div>
        </div>
      </el-col>
      <el-col :span="8" style="float: right;margin-right: 10px;">
        <img src="../../assets/tplbg.png" height="120" width="150">
      </el-col>
    </el-row>
    <el-dialog title="" :visible.sync="showDemo" width="80%">
      <img :src="demoUrl" style="width: 100%;margin-left: -20px;">
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="60%" style="text-align: left">
      <span class="help-span"></span>
      <span slot="footer" class="dialog-footer">
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
  import {SERVER_BASE} from "@/api/config";

  export default {
      name: "modleDownload",
      data(){
          return{
            showDemo: false,
            dialogVisible:false,
            demoUrl:require("../../assets/studentdemo.png"),
            InfoDownloadLink:SERVER_BASE+"/upload/basic",
          }
      },
      mounted() {
          this.loginControl();  //
      }
  }
</script>

<style scoped>
  .tpl {
    float: left;
    margin-top: 2%;
    margin-left: 10%;
    text-align: left;
    width: 60%;
    height: 200px;
  }
  .tpl img {
    margin-top: -120px;
    position: absolute;
  }
  .tpl .tpl-tit {
    font-size: 14px;
    color: #999;
  }
  .tpl .tpl-tit span{
    display: inline-block;
    margin-right: 6px;
  }
  .tpl .tpl-tit .act {
    color: #000;
  }
  .tpl .desc {
    margin-top: 30px;
    font-size: 14px;
    color: #888;
  }
  .tpl .btns {
    display: flex;
    margin-top: 50px;
    font-size: 14px;
    color: #1890FF;
  }
  .tpl .btns div {
    cursor: pointer;
    margin-right: 30px;
  }
  .tpl .btns a {
    color: #1890FF;
    text-decoration: none;
  }
  .help-span{
    color: #1890ff;
  }
</style>



