<template>
  <div>
    <el-upload
        :http-request="upload"
        action="localhost:8081/upload"
        class="upload"
        drag accept=".xls,.xlsx"
        multiple>
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">xls,xlsx</div>
    </el-upload>
    <el-dialog title="" :visible.sync="dialogTableVisible" width="80%">
      <el-table :data="freshmanTable" max-height="330" style="background:#fff;">
        <el-table-column fixed prop="build" label="" width="80"></el-table-column>
        <el-table-column fixed prop="roomNo" label="" width="80"></el-table-column>
        <el-table-column fixed prop="bunkId" label="" width="80"></el-table-column>
        <el-table-column prop="name" label="" width="120"></el-table-column>
        <el-table-column prop="studentNumber" label="" width="120"></el-table-column>
        <el-table-column prop="gender" label="" width="80"></el-table-column>
        <el-table-column prop="identifyNumber" label="" width="190"></el-table-column>
        <el-table-column prop="nation" label="" width="100"></el-table-column>
        <el-table-column prop="place" label="" width="100"></el-table-column>
        <el-table-column prop="school" label="" width="100"></el-table-column>
        <el-table-column prop="major" label="" width="150"></el-table-column>
        <el-table-column prop="grade" label="" width="80"></el-table-column>
        <el-table-column prop="studentClass" label="" width="80"></el-table-column>
        <el-table-column prop="status" label="" width="80"></el-table-column>
        <el-table-column prop="instructorId" label="" width="80"></el-table-column>
        <el-table-column prop="tel" label="" width="120"></el-table-column>
        <el-table-column prop="photo" label="" width="120"></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="unaccept"> </el-button>
        <el-button type="primary" @click="accept"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
    import {SERVER_BASE} from "@/api/config";
    import axios from 'axios'

    export default {
        name: "modleImport",
        data() {
            return {
                modleImport_action: SERVER_BASE + "/import/upload_basic",
                dialogTableVisible: false,
                freshmanTable: [
                    {
                        name: "",
                        studentNumber: "",
                        gender: "",
                        identifyNumber: "",
                        nation: "",
                        place: "",
                        build: "",
                        roomNo: "",
                        bunkId: "",
                        school: "",
                        major: "",
                        grade: "",
                        studentClass: "",
                        status: "",
                        instructorId: "",
                        tel: "",
                        photo: "",
                    }
                ]
            }
        },
        methods: {
            download(data) {
                if (!data) {
                    return
                }
                let blob = new Blob([data.data], {type: 'application/x-www-form-urlencoded'})
                let url = window.URL.createObjectURL(blob);
                console.log(url);
                let aLink = document.createElement("a");
                aLink.style.display = "none";
                aLink.href = url;
                aLink.setAttribute("download", name+".xlsx");
                document.body.appendChild(aLink);
                aLink.click();
                document.body.removeChild(aLink); //
                window.URL.revokeObjectURL(url); //blob
            },
            upload(param) {
                let that=this;
                let fileObj = param.file;
                let form = new FormData();
                form.append("file", fileObj);
                axios({
                    headers: {
                        'token': that.globolContext.token
                    },
                    method: 'post',
                    url: SERVER_BASE+'/import/upload',
                    data: form,
                    responseType: 'blob'
                }).then(function (resp) {
                    if(resp.status==200){
                        let blob=new Blob([resp.data]);
                        if(blob.size==0){
                            that.$message.success("")
                        }else{
                            that.$message.warning("")
                            that.download(resp)
                        }
                    }else{
                        that.$message.warning("")
                    }
                })
            },
            unaccept() {
                let that = this;
                that.dialogTableVisible = false;


            },
            accept() {
                let that = this;
                that.dialogTableVisible = false;

                that.$message.success("");
            }
        },
        mounted() {
            this.loginControl();  //
        }
    }
</script>

<style scoped>
  .upload {
    margin-top: 20px;
    float: left;
    margin-left: 10%;
  }
</style>



