<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span></span>
    </div>
    <el-table
            :data="tableData"
            style="width: 100%"
            max-height="350">
      <el-table-column label="" prop="name"></el-table-column>
      <el-table-column label="" prop="supervisorNumber"></el-table-column>
      <el-table-column label="" prop="gender"></el-table-column>
      <el-table-column prop="role" label="" width="90"></el-table-column>
      <el-table-column label="" prop="place"></el-table-column>
      <el-table-column label="" prop="watchSpan"></el-table-column>
      <el-table-column label="" prop="tel" width="150"></el-table-column>
      <el-table-column fixed="right">
        <template slot-scope="scope">
          <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="" :visible.sync="dialogFormVisible" width="30%">
      <el-form :model="dataLocal" style="text-align: left;margin: 0 auto;width: 300px;">
        <el-form-item label=":" :label-width="formLabelWidth">
          <span>{{dataLocal.name}}</span>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <span>{{dataLocal.supervisorNumber}}</span>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select class="subcheck" v-model="selected_build" clearable placeholder="">
            <el-option
                    v-for="item in builds"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>

  </el-card>
</template>

<script>
  import query from '@/api/query'
  import buildingInfo from '@/api/buildingInfo'
  import userManage from '@/api/userManage'

  export default {
    name:'distributeBox',
    data() {
      return{
          tableData:[],
          dialogFormVisible:false,
          builds:[],
          selected_build:'',
          form:{
              name:'',
              supervisorNumber:'',
              gender:'',
              role:'',
              place:'',
              watchSpan:'',
          },
          dataLocal:{
              name:'',
              supervisorNumber:'',
              watchSpan:'',
          },
          formLabelWidth: '50px'
      };
    },
    methods:{
        getallInfo(){
           let that=this;
           query.getsupervisorInfo()
               .then(function (resp) {
                   if(resp.data.code==200){
                       that.tableData=resp.data.data;
                       for (let gender = 0; gender < that.tableData.length; gender++) {
                           that.tableData[gender].role="";
                           if (that.tableData[gender].gender) {
                               that.tableData[gender].gender = "";
                           } else {
                               that.tableData[gender].gender = "";
                           }
                       }
                   }else{
                       console.log("")
                   }
               })
               .catch(function (err) {
                   console.log(err);
               })
        },
        getAllBuild(){
            let that = this;
            buildingInfo.getbuildingName().then(function (resp) {
                if (resp.data.code == 200) {
                    that.builds = resp.data.data.map(item => {
                        return {value: item, label: item + ''};
                    });
                }
            })
                .catch(function (err) {
                    console.log(err);
                });
        },
        handleEdit(index, row) {
            let that=this;
            that.selected_build='';
            that.dialogFormVisible = true;
            that.dataLocal={};
            that.dataLocal.name=that.tableData[index].name;
            that.dataLocal.supervisorNumber=that.tableData[index].supervisorNumber;
        },
        submit(){
            let that=this;
            that.dialogFormVisible=false;
            console.log(that.dataLocal);
            if(that.dataLocal!={}){
                let qs=require('qs');
                that.dataLocal.watchSpan=that.selected_build;
                console.log(that.dataLocal);
                userManage.updateWorkerPower(
                    qs.stringify({
                        workerNum: that.dataLocal.supervisorNumber,
                        buildNum: that.dataLocal.watchSpan,
                    },{ indices: false })
                ).then(function (resp) {
                    if(resp.data.code==200){
                        that.$message.success('');
                        that.getallInfo();
                    }else{
                        that.$message.warning('');
                    }
                })
                    .catch(function (err) {
                        that.$message.warning('');
                        console.log(err);
                    })
            }else{
                that.$message.err('');
            }
        }
    },
    mounted() {
        this.getallInfo();
        this.getAllBuild();
        this.loginControl();  //
    }
  };
</script>

<style scoped>
  .box-card{
    height: 36%;
    width: 88%;
    margin:  5% auto;
  }
</style>



